//Import
const dracoLoader = new DRACOLoader()
const gltfLoader = new GLTFLoader()
dracoLoader.setDecoderPath('/draco/')
gltfLoader.setDRACOLoader(dracoLoader)



//Mouse
const cursor = {
    x:0,
    y:0
}

window.addEventListener('mousemove', (event)=>{
    cursor.x = (event.clientX/sizes.width) - 0.5
    cursor.y = (event.clientY/sizes.height) - 0.5

    //console.log(cursor.y)

})

//Scroll
let scrollY = 0.01;


window.addEventListener('scroll', ()=>{
    scrollY = window.scrollY/ 400
    console.log(scrollY)

})


/**
 * Sizes
 */
const sizes = {}
sizes.width = window.innerWidth
sizes.height = window.innerHeight

window.addEventListener('resize', () =>
{
    // Save sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

})

/**
 * Environnements
 */
// Scene
const scene = new THREE.Scene()

// Camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.set(0,0,0)
scene.add(camera)


//Line test



//Particle

const particleGeometry = new THREE.BufferGeometry()
const pointMaterial = new THREE.PointsMaterial()
let particles

const createParticle = () =>{

    
    if(particles !== null){
        particleGeometry.dispose()
        pointMaterial.dispose()
        scene.remove(particles)
    }

    const positions = new Float32Array(debugObjects.vertexCount * 3)
    pointMaterial.size = debugObjects.particleSize
    pointMaterial.sizeAttenuation = debugObjects.sizeAttenuation

    for(let i = 0; i < debugObjects.vertexCount * 3; i++){
        positions[i] = (Math.random() - 0.5) * 30
    }

    particleGeometry.setAttribute( 'position', new THREE.BufferAttribute( positions, 3))
   
    particles = new THREE.Points(
        particleGeometry, pointMaterial
    )
    scene.add(particles)


}

createParticle()

//GUI

gui.add(debugObjects, 'sizeAttenuation').onFinishChange(createParticle)
gui.add(debugObjects, 'particleSize')
    .min(0.1)
    .max(1)
    .step(0.1)
    .onFinishChange(createParticle)
gui.add(debugObjects, 'vertexCount')
    .min(100)
    .max(1000)
    .step(1)
    .onFinishChange(createParticle)


